import React, { Component } from 'react';
import ListCard from './ListCard';
import { Dimmer, Loader, Segment } from 'semantic-ui-react';
import Client from './../modules/ApiUtils'; 
import { withRouter } from 'react-router-dom'

class SchoolsCard extends Component{
  
  constructor(props){
  	super(props);
  	this.state = { isLoading: true };
  }

  componentDidMount(){

    let { long, lat, r } = this.props;

  	Client.fetchSchools(long, lat, r).then((response) => {
  	  return response.json();
  	}).then((content) => {
      content = content.slice(0, 4);
      this.setState({ schools: content, isLoading: false});
  	}).catch((err) => {
      console.log(err);
    });
  }

  render(){
    let { isLoading, schools } = this.state;

    if(!isLoading){
      return(
        <ListCard heading="Schools" items={ schools } />
      );
    }else{
      return(
        <Segment style={{ 'minHeight': 300 }}>
        <Dimmer active>
          <Loader size="big" />
        </Dimmer>
        </Segment>
    );
    }
  }
}

export default withRouter(SchoolsCard);


