import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';
import { connect } from 'react-redux';
import { push } from 'connected-react-router';
import {
  Breadcrumb,
  Button,
  Container,
  Form,
  Grid,
  Icon,
  Input,
  Label,
  Menu,
  Responsive,
  Segment,
  Select,
  Sidebar,
  Visibility,
} from 'semantic-ui-react';
import Footer from './Footer';
import SchoolsCard from './SchoolsCard';
import './../../resources/css/search.css';

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
);

class MobileContainer extends Component {
  state = {};

  handlePusherClick = () => {
    const { sidebarOpened } = this.state;
    if (sidebarOpened) this.setState({ sidebarOpened: false });
  }

  handleToggle = () => this.setState({ sidebarOpened: !this.state.sidebarOpened });

  render() {
    const { children } = this.props;
    const { sidebarOpened } = this.state;

    return (
      <Responsive maxWidth={Responsive.onlyMobile.maxWidth}>
        <Sidebar.Pushable>
          <Sidebar as={Menu} animation='uncover' inverted vertical visible={sidebarOpened}>
            <Menu.Item id='brand'>
              AreaScout <sub>(NI)</sub>
            </Menu.Item>
            <Menu.Item as='a' active>Home</Menu.Item>
            <Menu.Item as='a'>Search</Menu.Item>
            <Menu.Item as='a'>About</Menu.Item>
            <Menu.Item as='a'>Contact</Menu.Item>
          </Sidebar>

          <Sidebar.Pusher
            dimmed={sidebarOpened}
            onClick={this.handlePusherClick}
            style={{ minHeight: '100vh' }}
          >
            <Segment
              inverted
              textAlign='center'
              style={{ minHeight: 350, padding: '1em 0em' }}
              vertical
            >
              <Container>
                <Menu inverted pointing secondary size='large'>
                  <Menu.Item onClick={this.handleToggle}>
                    <Icon name='sidebar' />
                  </Menu.Item>
                  <Menu.Item position='right'>
                    <Button as='a' inverted>
                      Log in
                    </Button>
                    <Button as='a' inverted style={{ marginLeft: '0.5em' }}>
                      Sign Up
                    </Button>
                  </Menu.Item>
                </Menu>
              </Container>
            </Segment>
            {children}
          </Sidebar.Pusher>
        </Sidebar.Pushable>
      </Responsive>
    )
  }
}

class DesktopContainer extends Component {
  state = {};
  hideFixedMenu = () => this.setState({ fixed: false });
  showFixedMenu = () => this.setState({ fixed: true });

  render() {
    const { children } = this.props;
    const { fixed } = this.state;

    return (
      <Responsive minWidth={Responsive.onlyTablet.minWidth} >
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment
            inverted
            textAlign='center'
            vertical
          >
          <div className="content">
            <Menu
              fixed={null}
              inverted={!fixed}
              pointing={!fixed}
              secondary={!fixed}
              size='large'
            >
              <Container textAlign='left'>
                <Menu.Item fitted='horizontally' id='brand' as='a' href='/'>AreaScout<sub>(NI)</sub></Menu.Item>
                <Menu.Item><Input 
                  icon={<Icon name='search' inverted circular link />} 
                  placeholder='Search...' 
                  size='large'
                  style={{
                    width: '400px',
                    marginBottom: '.5em'
                  }}
                /></Menu.Item>
                <Menu.Item className='' position='right'>
                  <Button as='a' inverted={!fixed} style={{ marginLeft: '1em' }}>
                    About
                  </Button>
                  <Button as='a' inverted={!fixed} style={{ marginLeft: '1em' }}>
                    Contact
                  </Button>
                </Menu.Item>
              </Container>
            </Menu>
            </div>
          </Segment>
        </Visibility>
        {children}
      </Responsive>
    )
  }
}

class Search extends Component{

  constructor(props){
    super(props);
    this.state = { query: '54 Ballyhenry Road, Comber, BT235JZ'};
  }

  componentWillMount(){
    let params = queryString.parse(this.props.location.search);
    if(!params['long'] || !params['lat'] || !params['r']){
      this.props.dispatch(push(`/notfound`));
    }else{
      this.setState({ long: parseFloat(params.long).toFixed(3), lat: parseFloat(params.lat).toFixed(3), r: parseInt(params.r) });
    }
    console.log(params);
  }

  render(){
    let { query } = this.state;
    let rangeOptions = [ { key: '1', value: '1', text: '1 km' },
                         { key: '2', value: '2', text: '2 km' },
                         { key: '3', value: '3', text: '3 km' },
                         { key: '4', value: '4', text: '4 km' },
                         { key: '5', value: '5', text: '5 km' } ]

  	return(
  	  <div id="Search">
  	  	<ResponsiveContainer>
        <Segment
          style={{ 'padding': '.5em 5.5em', 'borderBottom': '1px solid black' }}
          vertical
        >
          <Breadcrumb size='large'>
            <Breadcrumb.Section link>Home</Breadcrumb.Section>
            <Breadcrumb.Divider icon='right chevron' />
            <Breadcrumb.Section link>Search</Breadcrumb.Section>
          </Breadcrumb>
        </Segment>
        <Segment
          style={{ 'padding': '.5em 3em', 'borderBottom': '1px solid black' }}
          vertical
        >
        <Grid columns={3}>
          <Grid.Row textAlign='center'>
            <Grid.Column>
              <h3 style={{ 'marginLeft': 0 }}>{ query }</h3>
            </Grid.Column>
            <Grid.Column>
              
            </Grid.Column>
            <Grid.Column>
              <Form>
                <Form.Field>
                  <Select placeholder='5 km' options={ rangeOptions } />
                  <Label pointing>Enter a Range</Label>
                </Form.Field>
              </Form>
            </Grid.Column>
          </Grid.Row>
        </Grid>
        </Segment>
        <Segment style={{ 'padding': '1em 15em' }} vertical >
          <Grid columns={2} relaxed>
            <Grid.Row textAlign='center'>
              <Grid.Column>
                <SchoolsCard />
              </Grid.Column>
              <Grid.Column>
                <SchoolsCard long={ this.state.long } lat={ this.state.lat } r={ this.state.r } />
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Segment>
        <Footer />
        </ResponsiveContainer>
  	  </div>
  	);
  }

}

export default connect()(Search);


