import React, { Component, Link } from 'react';
import Utils from './../modules/ApiUtils';
import { Form, Icon, Input } from 'semantic-ui-react';
import { push } from 'connected-react-router';
import { connect } from 'react-redux';


class SearchBar extends Component{
  constructor(props){
  	super(props);
  	this.state = { loading: false, query: '' };
  	this.handleSubmit = this.handleSubmit.bind(this);
  	this.handleChange = this.handleChange.bind(this);
  }

  //fired when user types
  handleChange(e){
  	this.setState({ query: e.target.value });
  }

  //fired when user hits return or clicks search icon
  handleSubmit(e){
  	e.preventDefault();
  	this.setState({ loading: true });
  	let point = this.getLongLat(this.state.query);
  }

  getLongLat(query){
  	Utils.getLongLat(query).then((response) => {
  	  console.log(response);
      const { lng, lat } = response.results[0].geometry.location;
      this.props.dispatch(push(`/search?long=${encodeURIComponent(lng)}&lat=${encodeURIComponent(lat)}&r=2000`));
      return [lng, lat];
  	}).catch((err) => {
  	  this.setState({ loading: false });
  	  this.warn();
  	  console.log(err);
  	});
  }

  warn(){

  }

  render(){
  	let { loading } = this.state;
    return(
      <Form onSubmit={ this.handleSubmit }>
	    <Input
	    onChange = { this.handleChange }
	    value={ this.state.query } 
	    loading={ loading ? true : false }
	    icon={<Icon onClick={ this.handleSubmit } name='search' inverted circular link />} 
	    placeholder='Search...' 
	    size='big'
		  style={{
		    width: '500px',
		    marginTop: '2em'
		  }}
	    />
      </Form>
    );
  }
}

export default connect()(SearchBar);


